export default {
	data() {
		return {
			codeMsg: '发送验证码',
			count: 60,
			disable: false,
			checked: true,
			tel:'',// 用户登录填写手机号
			telCode: '',//验证码
			weChatPhone:'',//用户微信获取手机号
		};
	},
	mounted(){
		if(uni.getStorageSync('phoneNumber')){
			this.weChatPhone = uni.getStorageSync('phoneNumber')
		}
	},
	methods:{
		changeCheckbox(e){
			this.checked = !this.checked
		},
		// 获取验证码
		clickCodemsg(){
			if (!this.tel) {
				uni.showToast({
					title: '请输入手机号码',
					icon: 'none'
				})
				return
			}
			const rsg = /^[1][3,4,5,6,7,8][0-9]{9}$/
			if (this.tel.indexOf('.') !== -1 || !rsg.test(this.tel)) {
				uni.showToast({
					title: '请输入正确的手机号码',
					icon: 'none'
				})
				return
			}
			if(this.disable == false){
				this.$Request.get(`/appTelcodeController.do?getTelcode&tel=${this.tel}&type=2`).then(res => {
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
					if(res.code == '0') {
						let timer =  setInterval(() => {
							this.count -= 1
							this.codeMsg = this.count + 's'
							this.disable = true
							if(this.count < 0){
								clearInterval(timer)
								this.codeMsg = '发送验证码'
								this.count = 60
								this.disable = false
							}
						}, 1000)
					}
				}).catch(error => {
					console.log(error)
				})
			}
		},
		// 点击登录按钮
		clickLogin(){
			if(this.checked == false){
				uni.showToast({
					title: '请先阅读同意服务协议！',
					icon: 'none'
				});
			}else{
				if(!this.tel || !this.telCode){
					uni.showToast({
						title: '请检查输入项！',
						icon: 'none'
					});
				}else{
					// 用户手机登录
					this.$Request.get(`/appMemberController.do?loginMemberTel&tel=${this.tel}&telCode=${this.telCode}`)
					.then(res => {
						if(res.code == 0){
							uni.showToast({
								title: '登录成功，正在跳转...',
								icon: 'none'
							})
							uni.setStorageSync('userInfo', JSON.stringify(res.data.member))
							setTimeout(()=>{
								uni.switchTab({
									url:'/pages/myData/myData'
								})
							},1000)
						}else if(res.code == 100){
							// 尚未注册
							this.$Request.get(`/appMemberController.do?registerMember&memberTel=${this.tel}&openid=${uni.getStorageSync('openid')}`
							).then(res => {
								if(res.code == 0){
									uni.showToast({
										title: '登录成功，正在跳转...',
										icon: 'none'
									})
									uni.setStorageSync('userInfo', JSON.stringify(res.data.member))
									setTimeout(()=>{
										uni.switchTab({
											url:'/pages/myData/myData'
										})
									},1000)
								}else{
									uni.showToast({
										title: res.info,
										icon: 'none'
									})
								}
							})
						}else{
							uni.showToast({
								title: res.info,
								icon: 'none'
							});
						}
					})
				}
			}
		},
		// 小程序获取用户手机号
		onGetPhoneNumber(e){
			let vm = this
			if(e.detail.errMsg == 'getPhoneNumber:ok'){
				vm.$Request.postT('/wxController.do?getWxPhone',{
					ivdata:e.detail.iv,
					encrypdata: e.detail.encryptedData,
					sessionKey: uni.getStorageSync('sessionKey')
				},'application/json;charset=UTF-8').then(res => {
					if(res.code == 0){
						uni.setStorageSync('phoneNumber', JSON.parse(res.data.phone).phoneNumber)
						vm.weChatPhone = uni.getStorageSync('phoneNumber')
						vm.wechatLoginMember()
					}else{
						uni.showToast({
							title: res.info,
							icon: 'none'
						});
					}
				})
			}else{
				uni.showToast({
					title: '此程序需要获取您的授权才可进行登录！',
					icon: 'none'
				})
			}
		},
		// 小程序已有手机号，实现微信登录
		clickWechatLogin(){
			this.wechatLoginMember()
		},
		// 小程序查看是否已经注册过，已注册则可登录
		wechatLoginMember(){
			let vm = this
			vm.$Request.get(`/appMemberController.do?loginMember&openid=${uni.getStorageSync('openid')}`).then(res => {
				if(res.code == 0){
					// 已注册，可登录
					uni.showToast({
						title: '登录成功，正在跳转...',
						icon: 'none'
					})
					uni.setStorageSync('userInfo', JSON.stringify(res.data.member))
					setTimeout(()=>{
						uni.switchTab({
							url:'/pages/myData/myData'
						})
					},1000)
				}else if(res.code == 100){
					// 尚未注册
					vm.$Request.get(`/appMemberController.do?registerMember&memberTel=${uni.getStorageSync('phoneNumber')}&openid=${uni.getStorageSync('openid')}`
					).then(res => {
						if(res.code == 0){
							uni.showToast({
								title: '登录成功，正在跳转...',
								icon: 'none'
							})
							uni.setStorageSync('userInfo', JSON.stringify(res.data.member))
							setTimeout(()=>{
								uni.switchTab({
									url:'/pages/myData/myData'
								})
							},1000)
						}else{
							uni.showToast({
								title: res.info,
								icon: 'none'
							})
						}
					})
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		// 微信小程序点击勾选协议
		openProvider(){
			uni.showToast({
				title: '请先阅读同意服务协议！',
				icon: 'none'
			});
		},
		gotoUrl(url){
			uni.navigateTo({
				url:url
			})
		},
		// 微信登录,暂时没用到
		loginForProvider(type){
			var vm = this;
			let openid = uni.getStorageSync('openid')
			uni.getProvider({
			    service: 'oauth',
			    success: function (res) {
			        if (~res.provider.indexOf('weixin')) {
			        	uni.login({
			        		provider: 'weixin',
			        		success: function(loginRes) {
								// console.log(loginRes.code) // code
								vm.$Request.get(`/wxController.do?getOpenid&code=${loginRes.code}`).then(res => {
									if(res.errmsg){
										uni.showToast({
											title: res.errmsg,
											icon: 'none'
										})
									}
									// console.log(res) //openid
									if(res.openid){
										uni.setStorageSync('openid', res.openid)
									}
								})
								uni.getUserInfo({
									provider: 'weixin',
									success: (info) => { //这里请求接口
									   console.log(info);
									},
									fail: () => {
									   uni.showToast({title:"微信登录授权失败",icon:"none"});
									}
								 })
			        		},
			        		fail(err) {
			        			uni.showToast({
			        				title: '授权失败！',
			        				icon: 'none'
			        			});
			        		}
			        	});
			        }else{
						uni.showToast({
							title: '请先安装微信或升级版本',
							icon:"none"
						});
					}
			    }
			});
		},
	}
}
